#body{
  background-color: rgb(255, 249, 245);
}

.App {
  margin-left: 50px;
  margin-bottom: 35px;
}

#heading{
  width: 500px;
  font-size: 100px;
  line-height: 110px;
  color: rgb(184, 144, 144);
  margin: 20px 0; 
}

#textDiv{
  width: 330px;
  font-size: 14px;
  line-height: 28px;
  margin: 30px 0;
}

#submitBtn{
  height: 56px;
  width: 190px;
  border-radius: 28px;
  background-color: #2b2d30;
  color: rgb(192, 192, 192);
  border: none;
  outline: none;
  font-size: 14px;
  letter-spacing: 3.5px;
  transition: all 0.2s ease-in-out;
}

#navBar{
  position: fixed;
  right: 20px;
  top: 20px;
  padding: 0;
  background-color: transparent;
}

.navbar-toggler{
  position: fixed;
  right: 20px;
  top: 20px;
  border-radius: 50%;
  width: 75px;
  height: 75px;
  background-color: rgb(184, 144, 144);
  padding: 0;
  transition: all 0.2s ease-in-out;
}

.navbar-toggler.collapsed{
  background-color: #343A40;
}

.navbar-toggler.collapsed:hover,
#submitBtn:hover{
  background-color: rgb(184, 144, 144);
  color: white;
}

.navbar-toggler-icon{
  width: 50px;
  height: 50px;
}

.navbar-nav{
  background-color: rgb(136, 135, 135);
  position: fixed;
  right: 20px;
  top: 100px;
  width: 150px;
  padding: 10px;
  border-radius: 5px;
}

.nav-link{
  border-radius: 5px;
  padding: 10px;
  transition: all 0.2s ease-in-out;
}

.nav-link:hover{
  background-color: rgb(184, 144, 144);
}

@media screen and (max-width: 640px){

  .App {
    margin-left: 30px;
  }

  #heading{
    position: relative;
    top: 15vh;
    font-size: 50px;
    line-height: 55px;
    width: 300px;
  }

  #textDiv{
    position: relative;
    top: 15vh;
  }

  #submitBtn{
    position: relative;
    top: 25vh;
  }

  .navbar-toggler{
    width: 60px;
    height: 60px;
  }
}

@media screen and (max-width: 370px){
  #textDiv{
    width: 80vw;
  }

  #submitBtn{
    position: relative;
    top: 18vh;
  }
}